import React from "react";
import { useEffect, useState } from "react";
import "./App.css";
import mondaySdk from "monday-sdk-js";
import "monday-ui-react-core/dist/main.css";
//Explore more Monday React Components here: https://style.monday.com/
import Flex from "monday-ui-react-core/dist/Flex.js"
import TextField from "monday-ui-react-core/dist/Flex.js"

// Usage of mondaySDK example, for more information visit here: https://developer.monday.com/apps/docs/introduction-to-the-sdk/
const monday = mondaySdk();

const App = () => {
  const colTypes = new Set(["name", "board-relation", "multiple-person", "color", "date", "text", "long-text", "numeric", "dropdown"]);
  const [values, setValues] = useState([{ name: "dummy", type: "dummy"}])

  useEffect(() => {
    // Notice this method notifies the monday platform that user gains a first value in an app.
    // Read more about it here: https://developer.monday.com/apps/docs/mondayexecute#value-created-for-user/
    monday.execute("valueCreatedForUser");

    monday.get("context").then(res => {
      console.log(res)
      console.log(res.data)
      const boardId = res.data.boardId || 3715125693 // placeholder
      const query = `query { boards (ids: ${boardId}) { columns { title type id }}}`

      monday.api(query).then(res => {
        // name board-relation multiple-person color color date text long-text numeric dropdown
        const columns = res.data.boards[0].columns
        const filteredColumns = columns.filter(col => colTypes.has(col.type))
        console.log(filteredColumns)

        setValues(filteredColumns)
      })
    }).catch(err => {
      console.log(err)
    })
  }, []);

  return (
    <form className="App">
      <Flex
        wrap={true}
      >
        {values.map(val => (
          <fieldset>
            <label
              htmlFor={val.id}
            >
              {val.title}
            </label>
            <TextField
              id={val.id}
            />
          </fieldset>
        ))}
      </Flex>
    </form>
  );
};

export default App;
